const state = {
  navigation:false, //
  about_nav:2, //
  products_page:'',//
  solution_nav:'', //
  products_nav:41, //
  second_nav:0, //
  indexText:[], //
};
export default new Vuex.Store({
   state,
   mutations,
   actions,
   getter
});



